const structure = {
    1: {
        id: 'who',
        description: 'type in name (e.g.: "Mark")'
    },
    2: {
        id: 'what',
        description: 'type in activity (e.g.: "is playing hockey")'
    },
    3: {
        id: 'when',
        description: 'type in time / time area (e.g.: "in the afternoon")'
    },
    4: {
        id: 'where',
        description: 'type in place (e.g.: "at home")'
    }
}

export default structure;


