import React from 'react';
import structure from '../data/structure';
import WWWWStart from './WWWWStart';
import WWWWQuestion from './WWWWQuestion';

class WWWWContainer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            step: 0
        }
    }

    nextStep = step => {
        this.setState(pState => ({
            step: step 
                ? 1 
                : pState.step + 1,
        }));
    }

    render() {
        const {step} = this.state;
        const desc = structure && structure[step] && structure[step].description;
        const question = structure && structure[step] && structure[step].id;
        console.log(structure)
        console.log(step)
        return(
            <div className="wwww-container">
                <p>HELLO EVERYBODY</p>
                { step === 0 
                    ? 
                        (
                            <WWWWStart start={this.nextStep}/>
                        )
                    : 
                        (
                            <WWWWQuestion 
                                nextStep={this.nextStep} 
                                desc={desc} 
                                question={question} 
                            />
                        )     
                }              
            </div>
        )
    }
}

export default WWWWContainer;


